.card {
  /* border: 2px solid #92464657; */
  float: left;
  /* padding: 10px; */
  width: 100%;
}
.card-title{
  margin: 2px;
}
.card-body {
  margin: 5px;
  padding: 4px;
  border: 2px solid #c8c2c2;
}
td.round {
  width: 50px;
  text-align: center;
}
.round-color {
  border: 1px solid #20636f;
  padding: 2px 5px;
  /* background: #59eae5; */
  height: 17px;
  border-radius: 50%;
}
.download-div {
  position: absolute;
  top: 50%;
  left: 50%;
}
